.App{
    height: 180vh;
    width: 108vw;
    background-color: rgb(168,173,182);
    display: grid;
    place-items: center;
}
.app__container{
    height: 700px;
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(232,234,237);
    border-radius: 32px 0px;
    padding:32px 0;
}
.app__todoContainer{
    flex:1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}